.order-close {
  position: relative;
  background: transparent;
  border: none;
  margin-right: 30px;
}

.order-close:after, .order-close:before {
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  position: absolute;
  top: 0;
  left: 0;
  background: black;
  transform: rotate(45deg);
}

.order-close:before {
  transform: rotate(-45deg);
}

.order-wrapper .order-close {
  display: none;
