@media all and (max-width: 768px) {
  .order {
    position: relative;
    padding: 30px;
  }
  .order:after {
    content: "";
    display: block;
    clear: both;
  }
  .order-img {
    width: 50%;
    float: left;
  }
  .order-title {
    width: 50%;
    margin-bottom: 30px;
  }
  .order-close {
    position: absolute;
    right: -15px;
    top: 20px;
  }
  .order-sum {
    float: right;
    line-height: 0;
    margin-top: -10px;
  }
  .order-count {
    float: left;
  }
  .order-add {
    width: 100%;
  }
  .order-add-btn {
    margin-right: -20px;
  }
  .order .table {
    display: block;
  }
  .order .cell {
    display: inline-block;
  }
}

@media all and (max-width: 500px) {
  .order-img {
    width: 70%;
