.card-holder {
  position: relative;
  padding-bottom: 100%;
  display: block;
  color: #000;
}

.card-holder:hover .card-img-holder {
  transform: translate(0, -5.98291%);
}

.card-holder:hover .card-overlay {
  opacity: 1;
  z-index: 1;
}

.card-holder:hover .card-visible-overlay:after {
  transform: scale(1, 1);
  transition: all 0.3s;
}

.card-holder:hover .card-visible-overlay:before {
  transform: scale(1, 1);
  transition-delay: 0.3s;
}

.card-img-holder {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: all 0.3s;
}

.card-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  z-index: -9999;
  transition: opacity 0.2s;
}

.card-visible-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

.card-visible-overlay:after, .card-visible-overlay:before {
  content: '';
  display: block;
  position: absolute;
  right: 1px;
  background-color: currentColor;
}

.card-visible-overlay:after {
  top: 0;
  width: 1px;
  height: 100%;
  transform-origin: top;
  transform: scale(1, 0);
  transition: all 0.3s 0.3s;
}

.card-visible-overlay:before {
  bottom: 0;
  width: 100%;
  height: 1px;
  transform-origin: right;
  transform: scale(0, 1);
  transition: all 0.3s;
}

.card-btn {
